import { useState, useMemo, useRef, useEffect } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import {
  ArrowLeft,
  Trophy,
  Calendar,
  ClipboardCheck,
  ClipboardX,
  Users,
  Contact,
  MoreVertical,
  Trash2,
  UserRoundPlus,
  ArrowUpDown,
  FileText,
  Download,
  EllipsisVertical,
  AlertCircle,
} from "lucide-react"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import { InfoCard } from "@/components/users/info-card"
import { StatusBadge } from "@/components/documents/status-badge"
import { Badge } from "@/components/ui/badge"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog"
import { Pagination } from "@/components/competitions/pagination"
import type { Competition, CompetitionParticipant, CompetitionJudge } from "@/types/competition"
import { cn } from "@/lib/utils"
import { Tooltip, TooltipTrigger, TooltipContent } from "@/components/ui/tooltip"
import { useToast } from "@/hooks/use-toast"

interface CompetitionProfileProps {
  competition: Competition
  onDelete?: (competition: Competition) => void
}

// Компонент для текста с обрезкой и тултипом
function TruncatedTextWithTooltip({ text, className }: { text: string; className?: string }) {
  const textRef = useRef<HTMLParagraphElement>(null)
  const [isTruncated, setIsTruncated] = useState(false)

  useEffect(() => {
    const checkTruncation = () => {
      if (textRef.current) {
        setIsTruncated(textRef.current.scrollWidth > textRef.current.clientWidth)
      }
    }
    checkTruncation()
    window.addEventListener('resize', checkTruncation)
    return () => window.removeEventListener('resize', checkTruncation)
  }, [text])

  const textElement = (
    <p
      ref={textRef}
      className={`truncate ${className || ''}`}
    >
      {text}
    </p>
  )

  if (isTruncated) {
    return (
      <Tooltip>
        <TooltipTrigger asChild>
          {textElement}
        </TooltipTrigger>
        <TooltipContent>
          {text}
        </TooltipContent>
      </Tooltip>
    )
  }

  return textElement
}

export function CompetitionProfile({ competition, onDelete }: CompetitionProfileProps) {
  const router = useRouter()
  const { toast } = useToast()
  const [participantsPage, setParticipantsPage] = useState(1)
  const [judgesPage, setJudgesPage] = useState(1)
  const [participantsPageSize, setParticipantsPageSize] = useState(10)
  const [judgesPageSize, setJudgesPageSize] = useState(10)
  const [participantsSortField, setParticipantsSortField] = useState<"name" | "status" | null>(null)
  const [participantsSortDirection, setParticipantsSortDirection] = useState<"asc" | "desc">("asc")
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false)

  const participants = competition.participants || []
  const judges = competition.judges || []

  // Форматирование даты
  const formatDateRange = (startDate: string, endDate: string) => {
    const formatDate = (dateStr: string) => {
      const [day, month, year] = dateStr.split('.')
      const months = ['янв', 'фев', 'мар', 'апр', 'мая', 'июн', 'июл', 'авг', 'сен', 'окт', 'нояб', 'дек']
      return `${day} ${months[parseInt(month) - 1]}. ${year}`
    }
    
    const start = formatDate(startDate)
    const end = formatDate(endDate)
    
    if (startDate === endDate) {
      return start
    }
    return `${start} - ${end}`
  }

  // Сортировка участников
  const sortedParticipants = useMemo(() => {
    if (!participantsSortField) return participants
    
    return [...participants].sort((a, b) => {
      let aValue: string, bValue: string
      
      if (participantsSortField === "name") {
        aValue = a.fullName
        bValue = b.fullName
      } else {
        aValue = a.status
        bValue = b.status
      }
      
      const comparison = aValue.localeCompare(bValue, "ru")
      return participantsSortDirection === "asc" ? comparison : -comparison
    })
  }, [participants, participantsSortField, participantsSortDirection])

  const participantsTotalPages = Math.ceil(sortedParticipants.length / participantsPageSize)
  const paginatedParticipants = sortedParticipants.slice(
    (participantsPage - 1) * participantsPageSize,
    participantsPage * participantsPageSize
  )

  const judgesTotalPages = Math.ceil(judges.length / judgesPageSize)
  const paginatedJudges = judges.slice(
    (judgesPage - 1) * judgesPageSize,
    judgesPage * judgesPageSize
  )

  const handleParticipantsSort = (field: "name" | "status") => {
    if (participantsSortField === field) {
      setParticipantsSortDirection(participantsSortDirection === "asc" ? "desc" : "asc")
    } else {
      setParticipantsSortField(field)
      setParticipantsSortDirection("asc")
    }
  }

  const handleDeleteClick = () => {
    setDeleteDialogOpen(true)
  }

  const handleDeleteConfirm = () => {
    if (onDelete) {
      onDelete(competition)
    }
    setDeleteDialogOpen(false)
    router.push("/competitions")
    toast({
      title: "Соревнование удалено",
      variant: "success",
    })
  }



  return (
    <div className="min-h-screen bg-gray-50/50">
      <div className="max-w-[896px] mx-auto px-8 py-6">
        <div className="space-y-8">
          {/* Header Section */}
          <div className="flex items-start justify-between gap-4" style={{ height: '56px' }}>
            <div className="flex-1">
              <div className="flex items-start gap-3">
                <Link href="/competitions" className="pt-0.5">
                  <Button variant="outline" size="icon" className="h-9 w-9">
                    <ArrowLeft className="h-4 w-4" />
                  </Button>
                </Link>
                <div className="flex-1">
                  <div className="flex items-center gap-3 mb-1.5">
                    <h1 className="text-base font-semibold text-gray-900">{competition.name}</h1>
                    <StatusBadge status={competition.status} />
                  </div>
                  <div className="flex items-center gap-1">
                    <Calendar className="h-4 w-4 text-gray-600" />
                    <p className="text-sm text-gray-600">{formatDateRange(competition.startDate, competition.endDate)}</p>
                  </div>
                </div>
              </div>
            </div>
            <Button
              variant="outline"
              onClick={handleDeleteClick}
              className="gap-2"
            >
              <Trash2 className="h-4 w-4" />
              Удалить
            </Button>
          </div>

          {/* Competition Info Card */}
          <div className="bg-white border border-gray-200 rounded-[10px] p-6 shadow-sm -mt-3">
            <div className="flex items-center gap-3 mb-4">
              <div className="flex items-center justify-center w-11 h-11 bg-gray-50 rounded-lg flex-shrink-0">
                <Trophy className="h-5 w-5 text-gray-600" />
              </div>
              <div className="min-w-0 flex-1">
                <p className="text-sm font-semibold text-gray-900">{competition.organizer || "Организатор не указан"}</p>
                <p className="text-sm text-gray-600">Организатор</p>
              </div>
            </div>
            
            {/* Divider */}
            <div className="w-full h-px bg-gray-200 mb-4" />

            {/* Info Rows */}
            <div className="flex items-center gap-8 flex-wrap">
              <div className="flex items-center gap-2">
                {competition.inMinistryList ? (
                  <ClipboardCheck className="h-4 w-4 text-gray-600" />
                ) : (
                  <ClipboardX className="h-4 w-4 text-gray-600" />
                )}
                <div className="min-w-0">
                  <p className="text-sm text-gray-600">Включено в перечень Минспорта</p>
                  <p className="text-sm font-medium text-gray-900">{competition.inMinistryList ? "Да" : "Нет"}</p>
                </div>
              </div>

              <div className="w-px h-6 bg-gray-200" />

              <div className="flex items-center gap-2">
                <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg flex-shrink-0">
                  <Trophy className="h-4 w-4 text-gray-600" />
                </div>
                <div className="min-w-0">
                  <p className="text-sm text-gray-600">Дисциплина</p>
                  <p className="text-sm font-medium text-gray-900">{competition.discipline}</p>
                </div>
              </div>

              <div className="w-px h-6 bg-gray-200" />

              <div className="flex items-center gap-2">
                <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg flex-shrink-0">
                  <Users className="h-4 w-4 text-gray-600" />
                </div>
                <div className="min-w-0">
                  <p className="text-sm text-gray-600">Участников</p>
                  <p className="text-sm font-medium text-gray-900">{participants.length}</p>
                </div>
              </div>
            </div>
          </div>

          {/* Participants Card */}
          <div className="bg-white border border-gray-200 rounded-[10px] shadow-sm">
            <div className="px-6 pt-6 pb-3 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg">
                    <Contact className="h-4 w-4 text-gray-600" />
                  </div>
                  <h3 className="text-base font-semibold text-gray-900">Участники</h3>
                  <Badge variant="outline" className="text-xs font-semibold">
                    {participants.length}
                  </Badge>
                </div>
                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button variant="ghost" size="icon" className="h-8 w-8">
                      <EllipsisVertical className="h-4 w-4" />
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end">
                    <DropdownMenuItem>
                      <FileText className="h-4 w-4 mr-2" />
                      Сформировать документ
                    </DropdownMenuItem>
                    <DropdownMenuItem>
                      <Download className="h-4 w-4 mr-2" />
                      Скачать пакет документов
                    </DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>
              </div>
            </div>
            <div className="p-0">
              {participants.length > 0 ? (
                <div className="w-full">
                  <Table>
                    <TableHeader>
                      <TableRow className="border-b">
                        <TableHead className="px-6">
                          <button
                            className="flex items-center gap-1 text-sm font-medium text-muted-foreground hover:text-foreground transition-colors"
                            onClick={() => handleParticipantsSort("name")}
                          >
                            ФИО
                            <ArrowUpDown className={cn("h-3.5 w-3.5", participantsSortField === "name" && "text-foreground")} />
                          </button>
                        </TableHead>
                        <TableHead className="px-6">
                          <button
                            className="flex items-center gap-1 text-sm font-medium text-muted-foreground hover:text-foreground transition-colors"
                            onClick={() => handleParticipantsSort("status")}
                          >
                            Статус
                            <ArrowUpDown className={cn("h-3.5 w-3.5", participantsSortField === "status" && "text-foreground")} />
                          </button>
                        </TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {paginatedParticipants.map((participant) => (
                        <TableRow 
                          key={participant.id} 
                          className="border-b last:border-b-0 hover:bg-accent/20 transition-colors"
                        >
                          <TableCell className="px-6">
                            <div className="flex items-center gap-3">
                              <Avatar className="h-8 w-8 flex-shrink-0">
                                <AvatarFallback className="bg-gray-100 text-gray-700 text-xs">
                                  {participant.initials}
                                </AvatarFallback>
                              </Avatar>
                              <div className="min-w-0">
                                <TruncatedTextWithTooltip
                                  text={participant.fullName}
                                  className="text-sm font-medium text-gray-900"
                                />
                              </div>
                            </div>
                          </TableCell>
                          <TableCell className="px-6">
                            <StatusBadge status={participant.status} />
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                  {participantsTotalPages > 1 && (
                    <div className="px-6 py-4 border-t border-gray-200">
                      <Pagination
                        currentPage={participantsPage}
                        totalPages={participantsTotalPages}
                        pageSize={participantsPageSize}
                        onPageChange={setParticipantsPage}
                        onPageSizeChange={(size) => {
                          setParticipantsPageSize(size)
                          setParticipantsPage(1)
                        }}
                      />
                    </div>
                  )}
                </div>
              ) : (
                <div className="text-center py-12 text-sm text-gray-500">
                  Участники не найдены
                </div>
              )}
            </div>
          </div>

          {/* Judges Card */}
          <div className="bg-white border border-gray-200 rounded-[10px] shadow-sm">
            <div className="px-6 pt-6 pb-3 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg">
                    <Contact className="h-4 w-4 text-gray-600" />
                  </div>
                  <h3 className="text-base font-semibold text-gray-900">Судейский состав</h3>
                </div>
                <Button variant="outline" size="sm" className="gap-2">
                  <UserRoundPlus className="h-4 w-4" />
                  Назначить судью
                </Button>
              </div>
            </div>
            <div className="p-0">
              {judges.length > 0 ? (
                <div className="w-full">
                  <Table>
                    <TableHeader>
                      <TableRow className="border-b">
                        <TableHead className="px-6">ФИО</TableHead>
                        <TableHead className="px-6 w-[60px]"></TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {paginatedJudges.map((judge) => (
                        <TableRow 
                          key={judge.id} 
                          className="border-b last:border-b-0 hover:bg-accent/20 transition-colors"
                        >
                          <TableCell className="px-6">
                            <div className="flex items-center gap-3">
                              <Avatar className="h-8 w-8 flex-shrink-0">
                                <AvatarFallback className="bg-gray-100 text-gray-700 text-xs">
                                  {judge.initials}
                                </AvatarFallback>
                              </Avatar>
                              <div className="min-w-0">
                                <TruncatedTextWithTooltip
                                  text={judge.fullName}
                                  className="text-sm font-medium text-gray-900"
                                />
                              </div>
                            </div>
                          </TableCell>
                          <TableCell className="px-6" onClick={(e) => e.stopPropagation()}>
                            <DropdownMenu>
                              <DropdownMenuTrigger asChild>
                                <Button variant="ghost" size="icon" className="h-8 w-8">
                                  <MoreVertical className="h-4 w-4" />
                                </Button>
                              </DropdownMenuTrigger>
                              <DropdownMenuContent align="end">
                                <DropdownMenuItem>
                                  <Trash2 className="h-4 w-4 mr-2" />
                                  Удалить
                                </DropdownMenuItem>
                              </DropdownMenuContent>
                            </DropdownMenu>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                  {judgesTotalPages > 1 && (
                    <div className="px-6 py-4 border-t border-gray-200">
                      <Pagination
                        currentPage={judgesPage}
                        totalPages={judgesTotalPages}
                        pageSize={judgesPageSize}
                        onPageChange={setJudgesPage}
                        onPageSizeChange={(size) => {
                          setJudgesPageSize(size)
                          setJudgesPage(1)
                        }}
                      />
                    </div>
                  )}
                </div>
              ) : (
                <div className="text-center py-12 text-sm text-gray-500">
                  Судейский состав не назначен
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

